var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 mb-5"},[_vm._m(0),_vm._m(1),_c('p',{staticClass:"mb-5"},[_vm._v(" Es importante considerar que, para una implementación efectiva de la seguridad electrónica, se deben respetar las normativas legales y estándares de calidad, como los establecidos por la Organización Internacional de Normalización (ISO) y las leyes locales sobre privacidad y protección de datos. La Ley de Protección de Datos en diferentes países regula la recopilación y almacenamiento de imágenes y datos personales obtenidos mediante sistemas de seguridad, y la ISO/IEC 27001 establece pautas sobre la gestión de seguridad de la información en entornos organizacionales. ")]),_vm._m(2),_vm._m(3),_vm._m(4),_c('figcaption',[_vm._v("Fuente: OIT, 2024.")]),_c('div',{staticClass:"row bg6 align-items-center my-5"},[_c('div',{staticClass:"px-lg-5 px-4"},[_c('div',{staticClass:"bgw brad p-5 my-5"},[_c('CarouselRED',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-6 my-2"},[_c('h5',{staticClass:"mb-4"},[_vm._v(" 1. Importancia de la seguridad electrónica ")]),_c('p',[_vm._v(" Este campo es vital para garantizar la protección de activos, datos y personas. Los sistemas de seguridad electrónica abarcan desde dispositivos como cámaras de videovigilancia (CCTV) y sistemas de control de acceso hasta alarmas avanzadas, todos diseñados para responder a amenazas de manera preventiva y reactiva. La seguridad electrónica es fundamental en la reducción de riesgos asociados a accesos no autorizados y actividades sospechosas, permitiendo así una respuesta rápida y efectiva ante posibles incidentes. ")])]),_c('div',{staticClass:"col-lg-5 my-2"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/35.png"),"alt":""}})])]),_c('div',{staticClass: