},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-4 my-lg-0 my-3"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/38.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-8 my-lg-0 my-3"},[_c('p',[_vm._v(" Los sistemas de circuito cerrado de televisión (CCTV) constituyen una herramienta determinante en el ámbito de la seguridad electrónica, proporcionando una vigilancia continua de espacios específicos mediante cámaras conectadas a un sistema cerrado de monitores. Estos sistemas permiten una supervisión en tiempo real, y su implementación es común en edificios empresariales, zonas residenciales, instituciones públicas y áreas de acceso restringido. Los sistemas CCTV son altamente versátiles, ya que pueden configurarse para grabar imágenes, transmitir en directo o activar alertas en respuesta a ciertos movimientos o actividades. La calidad de imagen y las capacidades de almacenamiento han mejorado notablemente con el tiempo, ofreciendo imágenes en alta definición y opciones de almacenamiento en la nube, que facilitan el acceso a las grabaciones desde cualquier ubicación. ")])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-4 my-lg-0 my-3"},[_c('div',{staticClass:"bg3 p-4 brad h-100 j1"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Los sistemas de CCTV son una medida de seguridad disuasoria, ya que la presencia de cámaras puede reducir el número de incidentes, como robos o actos de vandalismo, al crear un entorno en el que las personas están conscientes de la vigilancia. Además, los videos capturados por estos sistemas pueden servir como evidencia en investigaciones, facilitando la identificación de personas o actividades específicas. Esta capacidad de registro es especialmente útil en casos donde se requiere documentación visual para procesos legales o investigaciones internas. ")])])]),_c('div',{staticClass:"col-lg-4 my-lg-0 my-3"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/39.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-4 my-lg-0 my-3"},[_c('div',{staticClass:"bg4 p-4 brad h-100 j1"},[_c('p',{staticClass:"mb-0"},[_vm._v(" En términos de normatividad, el uso de sistemas CCTV debe cumplir con regulaciones de privacidad y protección de datos en cada país. Por ejemplo, la Ley de Protección de Datos Personales establece directrices sobre la recopilación, tratamiento y almacenamiento de imágenes de personas, y en muchos casos, exige que las áreas vigiladas informen de la presencia de cámaras mediante avisos visibles. Asimismo, es necesario que los administradores de estos sistemas garanticen que las grabaciones estén protegidas contra accesos no autorizados, en concordancia con normativas como la ISO/IEC 27001, que regula la seguridad de la información. ")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center bg1 p-4 brad mb-5"},[_c('div',{staticClass:"col-lg-auto j1"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/40.png"),"alt":""}})]),_c('div',{staticClass:"col pt-lg-0 pt-md-4"},[_c('p',{staticClass:"mb-0"},[_vm._v(" En los últimos años, los sistemas CCTV han integrado tecnologías avanzadas, como la inteligencia artificial y el reconocimiento facial, lo cual amplía sus capacidades y eficiencia en la detección de eventos o comportamientos sospechosos. Estos avances permiten una respuesta más ágil ante situaciones de riesgo y una gestión más precisa de las incidencias. La analítica de video, por ejemplo, facilita el reconocimiento de patrones específicos, lo que resulta útil para la identificación de amenazas en tiempo real. Sin embargo, el uso de estas tecnologías debe gestionarse cuidadosamente para evitar problemas éticos relacionados con la privacidad. ")])])])
