},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-lg-8 my-lg-0 my-3"},[_c('div',{staticClass:"row justify-content-center bg4 p-4 brad mb-5"},[_c('div',{staticClass:"col-lg-auto j1"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/41.png"),"alt":""}})]),_c('div',{staticClass:"col pt-lg-0 pt-md-4"},[_c('p',{staticClass:"mb-0"},[_vm._v(" El control de acceso es una medida de seguridad que restringe y gestiona el ingreso a áreas específicas dentro de una instalación o edificio. Este sistema permite establecer niveles de autorización para cada persona, lo que garantiza que solo individuos con permisos específicos puedan acceder a zonas restringidas, reduciendo así el riesgo de intrusiones y protegiendo tanto los activos físicos como la información sensible. Los métodos de control de acceso varían desde tecnologías simples, como tarjetas de proximidad o claves de acceso, hasta sistemas más avanzados que integran autenticación biométrica (huellas digitales, reconocimiento facial, etc.) para asegurar que únicamente las personas autorizadas puedan ingresar a ciertos espacios. ")])])]),_c('p',[_vm._v(" Los sistemas de control de acceso no solo se implementan en áreas de alto riesgo, como salas de servidores o zonas de almacenamiento, sino también en edificios corporativos, hospitales, medios de comunicación, entidades estatales y áreas residenciales, donde es importante mantener un control de flujo de personas. Adicionalmente, estos sistemas suelen integrarse con otras soluciones de seguridad, como CCTV y alarmas, para ofrecer un monitoreo integral. Esta integración permite registrar eventos de acceso en tiempo real, generando informes detallados de actividad, lo cual es útil en caso de auditorías o investigaciones internas. ")])]),_c('div',{staticClass:"col-lg-4 my-lg-0 my-3"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/42.png"),"alt":""}})])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-4 my-lg-0 my-3"},[_c('img',{staticClass:"img-a img-t brad",attrs:{"src":require("@/assets/curso/temas/43.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-8 my-lg-0 my-3"},[_c('div',{staticClass:"bg2 p-5 brad j1 h-100"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Para que los sistemas de control de acceso cumplan su función de manera efectiva, es fundamental que se adhieran a normativas y estándares de seguridad. Por ejemplo, el estándar ISO/IEC 27001 establece requisitos para la protección de información, lo cual incluye el control de acceso a datos físicos y digitales. De la misma forma, los reglamentos locales de protección de datos son aplicables cuando el sistema recopila información de los usuarios, especialmente en caso de emplear biometría, ya que se considera un dato sensible. Cumplir con estos estándares garantiza la legalidad y efectividad del sistema, al tiempo que protege la privacidad de los individuos. ")])])])])
