},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-lg-8 my-lg-0 my-3 j1"},[_c('p',[_vm._v(" Los sistemas de alarma son componentes fundamentales de la seguridad electrónica, diseñados para detectar y alertar sobre situaciones de riesgo, como intrusiones, incendios, o emergencias en general. Estos sistemas funcionan mediante una combinación de sensores, paneles de control, y dispositivos de notificación (sirenas, luces o avisos remotos) que activan alertas en el momento en que se detecta una anomalía o se produce un evento que desencadena la alarma. En entornos comerciales y residenciales, los sistemas de alarma ayudan a prevenir robos y a mitigar daños mediante la detección temprana de amenazas. ")]),_c('div',{staticClass:"row justify-content-center bg1 p-4 brad mb-4"},[_c('div',{staticClass:"col-lg-auto j1"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/44.png"),"alt":""}})]),_c('div',{staticClass:"col pt-lg-0 pt-md-4"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Existen diversos tipos de sistemas de alarma, adaptados a las necesidades y características de cada espacio. Las alarmas antirrobo, por ejemplo, suelen emplear sensores de movimiento y de apertura en puertas y ventanas, mientras que las alarmas contra incendios utilizan detectores de humo, calor o gases tóxicos. Cada sistema se puede personalizar de acuerdo con el nivel de riesgo, la distribución de los espacios, y el tipo de amenaza que se desea mitigar. Las alarmas también pueden integrarse con sistemas de videovigilancia y control de acceso para ofrecer una solución de seguridad completa, que facilite el monitoreo y la gestión de incidentes. ")])])])]),_c('div',{staticClass:"col-lg-4 my-lg-0 my-3"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/45.png"),"alt":""}})])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-lg-4 my-lg-0 my-3"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/46.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-8 my-lg-0 my-3 j1"},[_c('p',[_vm._v(" El uso de sistemas de alarma debe cumplir con normativas específicas de seguridad y protección. En muchos países, los sistemas de detección y alerta de incendios, por ejemplo, están regulados por estándares nacionales e internacionales que establecen las características y especificaciones técnicas de los equipos. También existen requisitos en cuanto a la instalación, mantenimiento y supervisión de estos sistemas, como los que se encuentran en la Norma NFPA 72 para sistemas de detección de incendios y en las directrices de seguridad de la ISO 7240-1. Estas regulaciones aseguran que los sistemas funcionen de manera confiable y efectiva en situaciones de emergencia. ")]),_c('div',{staticClass:"bg4 p-4 brad h-100 j1"},[_c('p',{staticClass:"mb-0"},[_vm._v(" En términos de innovación, los sistemas de alarma han evolucionado significativamente en los últimos años. Hoy en día, muchos sistemas están conectados a plataformas de monitoreo remoto y ofrecen la posibilidad de recibir alertas directamente en dispositivos móviles. Esto permite a los propietarios y responsables de seguridad supervisar el estado del sistema en tiempo real y responder rápidamente ante cualquier alerta. La tecnología moderna también permite la implementación de alarmas inteligentes, que utilizan inteligencia artificial para diferenciar entre falsas alarmas y amenazas reales, reduciendo así la frecuencia de activaciones innecesarias. ")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-end mb-4"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/47.png"),"alt":""}})]),_c('div',{staticClass:"col pt-lg-0 pt-md-4"},[_c('div',{staticClass:"bg3 p-4 j1"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Su implementación permite la activación de planes de evacuación, cierres de rutas de acceso y coordinación de recursos, minimizando el impacto de estos eventos en la población y en la infraestructura local. En algunos territorios, estas alarmas se integran con redes de sensores y estaciones meteorológicas que monitorean en tiempo real variables como la humedad, temperatura y movimientos de tierra, permitiendo a las autoridades anticiparse a fenómenos que podrían poner en riesgo a las personas y los ecosistemas. ")])])])])
