      <div id="t_3_3" class="titulo-segundo color-acento-contenido">
        <h2>3.3 Sistemas de alarma</h2>
      </div>
      <div class="row justify-content-center mb-4">
        <div class="col-lg-8 my-lg-0 my-3 j1">
          <p>
            Los sistemas de alarma son componentes fundamentales de la seguridad
            electrónica, diseñados para detectar y alertar sobre situaciones de
            riesgo, como intrusiones, incendios, o emergencias en general. Estos
            sistemas funcionan mediante una combinación de sensores, paneles de
            control, y dispositivos de notificación (sirenas, luces o avisos
            remotos) que activan alertas en el momento en que se detecta una
            anomalía o se produce un evento que desencadena la alarma. En
            entornos comerciales y residenciales, los sistemas de alarma ayudan
            a prevenir robos y a mitigar daños mediante la detección temprana de
            amenazas.
          </p>
